import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Button } from '@components/ui/button.jsx';
import { Input } from '@components/ui/input.jsx';
import { Label } from '@components/ui/label.jsx';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@components/ui/card.jsx';

const API_BASE_URL = import.meta.env.VITE_API_BASE_URL;

const LoginPage = () => {
  const navigate = useNavigate();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [isLoginMode, setIsLoginMode] = useState(true); // true for login, false for create account
  const [message, setMessage] = useState('');
  const [error, setError] = useState('');

  const handleMicrosoftLogin = async () => {
    try {
      setMessage('');
      setError('');
      
      // Solicitar URL de autorização do backend
      const response = await fetch(`${API_BASE_URL}/api/microsoft/login`, {
        credentials: 'include'
      });
      
      const data = await response.json();
      
      if (data.success) {
        // Redirecionar para Microsoft
        setMessage('Redirecionando para Microsoft...');
        window.location.href = data.auth_url;
      } else {
        setError('Erro ao iniciar login com Microsoft');
      }
    } catch (error) {
      setError('Erro de conexão com o servidor');
    }
  };

  const handleLocalAuth = async (e) => {
    e.preventDefault();
    setMessage('');
    setError('');

    const endpoint = isLoginMode ? `${API_BASE_URL}/api/auth/login` : `${API_BASE_URL}/api/auth/register`;
    const method = 'POST';
    const body = JSON.stringify({ email, password });

    try {
      const response = await fetch(endpoint, {
        method,
        headers: { 'Content-Type': 'application/json' },
        body,
      });

      const data = await response.json();

      if (response.ok) {
        setMessage(data.message || (isLoginMode ? 'Login bem-sucedido!' : 'Conta criada com sucesso!'));
        console.log('Sucesso:', data);
        
        // Redirecionar após login bem-sucedido
        if (isLoginMode) {
          // Aguardar um pouco para mostrar a mensagem de sucesso
          setTimeout(() => {
            navigate('/dashboard');
          }, 1000);
        } else {
          // Para registro, mostrar mensagem e mudar para modo login
          setTimeout(() => {
            setIsLoginMode(true);
            setMessage('Conta criada! Agora faça login.');
            setEmail('');
            setPassword('');
          }, 1500);
        }
      } else {
        setError(data.message || 'Ocorreu um erro.');
        console.error('Erro:', data);
      }
    } catch (err) {
      setError('Não foi possível conectar ao servidor. Tente novamente mais tarde.');
      console.error('Erro de rede:', err);
    }
  };

  return (
    <div className="flex items-center justify-center min-h-screen bg-gray-100">
      <Card className="w-full max-w-md">
        <CardHeader className="space-y-1">
          <CardTitle className="text-2xl text-center">
            {isLoginMode ? 'Entrar' : 'Criar Conta'}
          </CardTitle>
          <CardDescription className="text-center">
            {isLoginMode
              ? 'Escolha como deseja acessar o dashboard' 
              : 'Crie sua conta para acessar o dashboard'}
          </CardDescription>
        </CardHeader>
        <CardContent className="grid gap-4">
          <Button variant="outline" className="w-full" onClick={handleMicrosoftLogin}>
            Entrar com Microsoft Entra ID
          </Button>
          <div className="relative">
            <div className="absolute inset-0 flex items-center">
              <span className="w-full border-t" />
            </div>
            <div className="relative flex justify-center text-xs uppercase">
              <span className="bg-background px-2 text-muted-foreground">
                Ou continue com
              </span>
            </div>
          </div>
          <form onSubmit={handleLocalAuth} className="grid gap-4">
            <div className="grid gap-2">
              <Label htmlFor="email">Email</Label>
              <Input
                id="email"
                type="email"
                placeholder="m@example.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>
            <div className="grid gap-2">
              <Label htmlFor="password">Senha</Label>
              <Input
                id="password"
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>
            {message && <p className="text-green-500 text-center">{message}</p>}
            {error && <p className="text-red-500 text-center">{error}</p>}
            <Button type="submit" className="w-full">
              {isLoginMode ? 'Entrar com Conta Local' : 'Criar Conta'}
            </Button>
          </form>
          <Button variant="link" className="w-full" onClick={() => setIsLoginMode(!isLoginMode)}>
            {isLoginMode ? 'Não tem uma conta? Crie uma!' : 'Já tem uma conta? Faça login!'}
          </Button>
          {!isLoginMode && (
            <Button variant="link" className="w-full">
              Esqueceu a senha?
            </Button>
          )}
        </CardContent>
      </Card>
    </div>
  );
};

export default LoginPage;


